import React from 'react';
import './scss/Eduzone_channel.scss';
import { Link } from "react-router-dom";
import no_data from './svg/not_found.svg';
import {Servererror,NotfoundError} from './Diya_error';
class Channel extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            channel_ajax_done: false,
            channel_ajax_error:false,
            channel_loaded: false,
            channel_date: [],
            channel_error:''
        }
    }
    componentDidMount() {
        this.read_channels()
    }
    read_channels() {
        fetch(window.json_server + "channel/" + this.props.match.params.id, {
            'cache': 'no-store'
        })
            .then(response => response.json())
            .then((jsonData) => {
                if (jsonData.response === 0) {
                    window.location = window.login_url;
                }
                if (jsonData.response === 200) {
                    this.setState({
                        channel_date: jsonData.data,
                        channel_loaded: true,
                        channel_ajax_done: true,
                    })
                }
                else if (jsonData.response === 404) {
                    this.setState({
                        channel_loaded: false,
                        channel_ajax_done: true,
                        channel_error:jsonData.message

                    })
                }else{
                    window.location = window.login_url + window.location.pathname
                }
            })
            .catch((error) => {
                this.setState({
                    channel_ajax_done: true,
                    channel_ajax_error:true
                })
            });
        window.scroll({
            top: 0,
            left: 0,
            behavior: 'smooth'
        });
    }
    render() {
        return (
            <div>
                {this.state.channel_ajax_done === false ? <img className="diya_load" alt="" src="https://i.ya-webdesign.com/images/loading-gif-png-5.gif" /> : null}
                {this.state.channel_loaded === true && this.state.channel_ajax_done === true ?
                    <div>
                        <div className="channel_profile">
                            <div className="twPc-div">
                                <div>

                                    <a title="" href="#a" className="twPc-avatarLink">
                                        <img alt={this.state.channel_date.name}
                                            src={window.media_server + this.state.channel_date.icon}
                                            className="twPc-avatarImg" />
                                    </a>


                                    <div className="twPc-divStats">
                                        <div className="twPc-divUser">
                                            <div className="twPc-divName">
                                                <a href="#a">{this.state.channel_date.name}</a>
                                            </div>
                                            <span>
                                                <a href="#a">@<span>{this.state.channel_date.uri}</span></a>
                                            </span>
                                        </div>
                                        <ul className="twPc-Arrange">
                                            <li key="0">
                                                <span>Premium Channel</span>
                                                <span>Rs1/D</span>
                                            </li>
                                            <li key="2">
                                                <button>Subscribe</button>

                                            </li>
                                        </ul>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="channel_programme_title"><i className="fa fa-hand-o-right"></i>Classes/Programmes</div>
                        <div className="channel_programmes">
                            {this.state.channel_date.programmes.length <= 0 ?
                                <div className="channel_blank"><NotfoundError error={"No Data"}/></div> :
                                <div className="row no-gutters">
                                    {this.state.channel_date.programmes.map(programmes => {
                                        return (
                                            <div className="col-6 col-sm-3 col-md-2">
                                                <Link to={"/channel/" + this.props.match.params.id + "/programme/" + programmes.uri}>
                                                    <div className="card">
                                                        {programmes.premium === false ?
                                                        <i className="fa fa-unlock"></i>
                                                        :null}
                                                        <img alt=""
                                                            src={window.media_server + programmes.icon} />
                                                        <p>{programmes.name}</p>
                                                    </div>
                                                </Link>
                                            </div>

                                        )
                                    })}
                                </div>
                            }
                        </div>
                        <div className="channel_programme_title"><i className="fa fa-info-circle"></i>About</div>

                    </div>
                    : null}
                    {this.state.channel_loaded === false && this.state.channel_ajax_done === true && this.state.channel_ajax_error === false ? <NotfoundError error={this.state.channel_error}/>:null}
                    {this.state.channel_ajax_done === true && this.state.channel_ajax_error === true ?  <Servererror error={this.state.channel_error} />:null}
            </div>
        )
    }
}
export default Channel;