import React from 'react';
import "./scss/Eduzone_programmes.scss";
import Diyads from "./Diya_ads";
import Data from "./data/programme.json";
import { Link } from "react-router-dom";
import { NotfoundError, Servererror } from './Diya_error'
class Programmes extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
            channel: this.props.match.params.id,
            programme: this.props.match.params.pgm,
            data: Data,
            demo: false,
            error_msg: '',
            width:1,
            pagit:1
        }
        this.loadepage = this.loadepage.bind(this);
        this.read_programmes = this.read_programmes.bind(this);
        this.newpage = this.newpage.bind(this);
        this.oldpages = this.oldpages.bind(this);
    }
    componentDidMount() {
        this.read_programmes()
    }
    loadepage(){
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
            pagit:1
        })
        this.read_programmes()
    }
    newpage(){
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
            pagit:1
        })
        this.read_programmes()
    }
    oldpages(){
        this.setState({
            ajax_done: false,
            ajax_error: false,
            data_loaded: false,
            pagit:1
        })
        this.read_programmes()
    }
    async read_programmes() {
        await fetch(window.json_server + this.state.channel + "/programme/" + this.state.programme+"?p="+this.state.pagit, {
            'cache': 'no-store'
        })
            .then(response => response.json())
            .then((jsonData) => {
                window.scroll({
                    top: 0,
                    left: 0,
                    behavior: 'smooth'
                });
                if (jsonData.response === 200) {
                    // jsonData is parsed json object received from url
                    this.setState({
                        data: jsonData.data,
                        width:jsonData.width,
                        data_loaded: true,
                        ajax_done: true,
                        demo: jsonData.demo
                    })
                }
                else if (jsonData.response === 404) {
                    // jsonData is parsed json object received from url
                    this.setState({
                        data_loaded: false,
                        ajax_done: true,
                        ajax_error: false,
                        error_msg: jsonData.message
                    })
                }else{
                    window.location = window.login_url + window.location.pathname
                }
            })
            .catch((error) => {
                this.setState({
                    data_loaded: false,
                    ajax_done: true,
                    ajax_error: true,
                })
                console.log(error)
            });
    }
    render() {
        return (
            <div>
                <Diyads />

                <div>
                    {this.state.ajax_error === false ?
                        <div className="date_slider">
                            <div className="row">
                                <div className="date_slide col-3 col-md-3 col-lg-3">
                                    <button onClick={this.loadepage}>Latest</button>
                                </div>
                                <div className="date_slide col-3 col-md-3  col-lg-3">
                                    <button><i className="fa fa-arrows-h"></i></button>
                                </div>
                                
                                <div className="date_slide col-3 col-md-3  col-lg-3">
                                    {this.state.pagit > 1 ? <button><i className="fa fa-angle-double-left"></i> Newer</button>:null}
                                </div>
                                <div className="date_slide col-3  col-md-3  col-lg-3">
                                {this.state.width !== 0  ?<button>Older <i className="fa fa-angle-double-right"></i></button>:null}
                                </div>
                            </div>
                        </div>
                        : null}
                    {this.state.ajax_done === true && this.state.data_loaded === true ?
                        <div className="programme_sheet">
                            {this.state.data_loaded === true ?
                                <div className="name_set">
                                    {this.state.data.map(programme => {
                                        return (
                                            <a href="#a">
                                                <div className="item">{programme.name}</div>
                                            </a>)
                                    })}
                                </div>
                                : null}
                            {this.state.data_loaded === true ?
                                <div className="programme_set">
                                    {this.state.demo === true ?
                                        <div className="banner" style={{width:this.state.width*280+this.state.width*100}}>

                                            {this.state.data.map(conent => {
                                                return (
                                                    <div className="item">
                                                        {conent.playlist.filter(({ demo }) => demo === "True").map(playlist => {
                                                            return (
                                                                <div>
                                                                    <Link to={"/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" + btoa(playlist.uri)}>
                                                                        <div className="item-box">
                                                                            <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                            <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                        </div>
                                                                    </Link>
                                                                </div>
                                                            )
                                                        })}
                                                        {conent.playlist.filter(({ demo }) => demo === "False").map(playlist => {
                                                            return (
                                                                <div>
                                                                    <Link to={playlist.uri}>
                                                                        <div className="item-box demo">
                                                                            <i className="fa fa-lock"></i>
                                                                            <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                            <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                        </div>
                                                                    </Link>
                                                                </div>
                                                            )
                                                        })}
                                                    </div>
                                                )
                                            })}

                                        </div> :
                                        <div className="banner">
                                            {this.state.data.map(conent => {
                                                return (
                                                    <div className="item">
                                                        {conent.playlist.map(playlist => {
                                                            return (
                                                                <div>
                                                                    <Link to={"/channel/" + this.state.channel + "/programme/" + this.state.programme + "/u/" +  btoa(playlist.uri)}>
                                                                        <div className="item-box">
                                                                            <div className="box-image" ><img alt="" src={window.media_server + playlist.icon} /></div>
                                                                            <div className="box-content" ><b>{playlist.name}</b><br />{playlist.info}<br />{playlist.date}</div>
                                                                        </div>
                                                                    </Link>
                                                                </div>

                                                            )
                                                        })}

                                                    </div>
                                                )
                                            })}
                                        </div>
                                    }
                                </div>
                                : null}
                        </div>

                        : null}
                {this.state.ajax_done === false ? <img className="diya_load" alt="" src="https://i.ya-webdesign.com/images/loading-gif-png-5.gif" /> : null}
    
                </div>
                {this.state.ajax_done === true && this.state.data_loaded === false ?
                    <NotfoundError error={this.state.error_msg} /> : null}
                {this.state.ajax_done === true && this.state.ajax_error === true ?
                    <Servererror error={this.state.error_msg} /> : null}
            </div>
        )
    }
}
export default Programmes;