    {
        jQuery(document).trigger(new jQuery.Event("fullscreenchange"));
    }
    
    /**
     * Handles the browser-specific fullscreenerror event and triggers
     * a jquery event for it.
     *
     * @param {?Event} event
     *            The fullscreenerror event.
     */
