import React from 'react';
import eimg from './svg/no_data.svg'
import Servererror_img from './svg/errorserver.svg';
export function Servererror(error) {
    function reloadwindow(){
        window.location.reload()
    }
    function gobavkadwindow(){
        window.history.back()
    }
    return (
        <div className="diya_error_page">
            <img style={{ width: '100%' }} src={Servererror_img} alt="error" />
            <h2>Page Could not be loaded</h2>
            <div>
            <ul>
                <li>No Internet</li>
                <li>Internal Server Error</li>
                <li>Server Not Responding</li>
            </ul>
            </div>
            <p><button onClick={reloadwindow}><i className="fa fa-refresh"></i> Reload</button></p>
            <p><a title="hello" onClick={gobavkadwindow} href="#goback"><i className="fa fa-arrow-left"></i> Go Back</a></p>
        </div>
    )
}
export function NotfoundError(error) {
    function gobavkadwindow(){
        window.history.back()
    }
    return (
        <div className="diya_error_page">
            <img style={{ width: '100%' }} src={eimg} alt="error" />
            <h2>{error.error}</h2>
            <p><button onClick={gobavkadwindow}><i className="fa fa-arrow-left"></i> Go Back</button></p>
        </div>
    )
}
